import { Avatar, Badge, Button, Descriptions, List, Table, Tooltip, Modal, Tag } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux'
import style from './index.module.scss';
import zip_icon from '../../assets/images/zip.png'
import { ColumnsType } from 'antd/lib/table';
import { FileUpload, history_list, history_remove, injection } from '../../http/api';
import { Percentage, renderSize } from '../../utils';
import { loading_status, loading_text, loading_visible } from '../../redux/actions/project';
const { confirm } = Modal;

interface DataType {
    name: string;
    remark: string;
    port?: string;
    key?: string;
    host?: string;
    history?: string;
    date?: string;
    status?: string;
    process?: any;
}

interface HistoryFile {
    date: number;
    dateText: string;
    fileName: string;
    size: number
}


interface Complete { text: string; time?: number; err?: boolean, callback?: () => void }


const View: React.FC = (props: any) => {
    const details: DataType = props.siteDetails




    // 
    const [buildZip, setBuildZip] = useState<File | null>(null)


    const [historyFiles, sethistoryFiles] = useState<HistoryFile[]>([])





    const columns: ColumnsType = [

        {
            title: "",
            dataIndex: 'fileName',
            key: 'fileName',
            width: '100px',
            render: (_, { fileName }) => (
                <>
                    <img width={22} height={22} src={zip_icon} />
                    <span style={{ marginLeft: '10px' }}>{fileName}</span>
                </>
            ),
        },
        {
            title: '',
            dataIndex: 'dateText',
            key: 'date',
            width: '120px',
        },
        {
            title: '',
            dataIndex: 'size',
            key: 'date',
            width: '60px',
            render: (_, { size }) => (
                <>
                    <span style={{ marginLeft: '10px' }}>{renderSize(size)}</span>
                </>
            ),


        },
        {
            title: '',
            dataIndex: 'operate',
            key: 'operate',
            width: '120px',
            render: (_, { key }) => (
                <>
                    <Button onClick={() => _injection(key)} style={{ marginLeft: '10px' }} type="primary" size="small"></Button>
                    <Button onClick={() => _history_remove(key)} style={{ marginLeft: '10px' }} size="small"></Button>
                </>
            ),
        },

    ];


    function complete({ text, time = 800, err, callback }: Complete) {
        const time1 = setTimeout(() => {
            props.loading_status(err ? 'fail' : 'success')
            props.loading_text(text)
            clearTimeout(time1)
        }, 300);

        const time2 = setTimeout(() => {
            props.loading_visible(false)
            callback && callback()
            clearTimeout(time2)
        }, time);
    }



    async function _history_remove(file_key: string) {
        confirm({
            title: '',
            icon: <ExclamationCircleOutlined />,
            content: `${file_key}`,
            okText: '',
            okType: 'danger',
            cancelText: '',
            async onOk() {
                props.loading_visible(true)
                const res = await history_remove({ id: details.key, file_key })
                if (res.code === 200) {
                    { }
                    complete({ text: '', callback: _history_list })
                } else {
                    complete({ text: `${res.msg}`, err: true, time: 2000 })
                }

            },
            onCancel() {
                console.log('Cancel');
            },
        });

    }


    async function _history_list() {
        const res = await history_list({ id: details.key })
        if (res.code === 200) {
            sethistoryFiles(res.data.sort((a, b) => b.date - a.date))
        }
    }

    async function _injection(file_key: string) {
        props.loading_visible(true)
        const res = await injection({ id: details.key, file_key })
        if (res.code === 200) {
            { }
            complete({ text: '' })
        } else {
            complete({ text: `${res.msg}`, err: true, time: 2000 })
        }
    }


    useEffect(() => {
        _history_list()
    }, [])


    useEffect(() => {
        console.log('===');

    }, [props.siteDetails.status])




    // 
    function fileSelectClick() {
        const input: HTMLInputElement = document.createElement("input");
        input.type = "file";
        input.multiple = false;
        input.onchange = async (e: any) => {
            props.loading_visible(true)
            const formdata = new FormData()
            formdata.append('file', e.target.files[0])
            formdata.append('id', details.key)
            const res = await FileUpload(formdata, (progressEvent: any) => {
                const schedule = Percentage(
                    progressEvent.loaded,
                    progressEvent.total
                );
            })
            if (res.code === 200) {
                complete({ text: '', callback: _history_list })
            } else {
                complete({ text: `${res.msg}`, err: true, time: 2000 })
            }

        };
        input.click();
    }



    return (
        <div className={style.container}>
            <Descriptions size='small' bordered>
                <Descriptions.Item label="" >{details.name}</Descriptions.Item>
                <Descriptions.Item label="ID" span={2}>{details.key}</Descriptions.Item>
                <Descriptions.Item label="" >
                    <Tag color={'green'}>
                        {details.port}
                    </Tag>
                </Descriptions.Item>

                <Descriptions.Item label="" span={2}>
                    <Badge status={details.status === '1' ? 'warning' : 'error'} text={details.status === '1' ? '' : ''} />
                </Descriptions.Item>
                <Descriptions.Item label="" >{details.date}</Descriptions.Item>
                <Descriptions.Item label="" span={2}>
                    <a target="_blank" href={`http://${window.location.hostname}:` + details.port}>{window.location.hostname + ':' + details.port}</a>
                </Descriptions.Item>

                <Descriptions.Item label="" >
                    {details.remark}
                </Descriptions.Item>
            </Descriptions>

            <Button onClick={fileSelectClick} type="primary" style={{ marginTop: '12px' }}></Button>
            {details.status === '0' && <Button onClick={() => props.process_start(details.key)} style={{ marginTop: '12px', marginLeft: '12px' }} type='dashed'></Button>}
            {details.status === '1' && <Button onClick={() => props.process_kill(details.key)} style={{ marginTop: '12px', marginLeft: '12px' }} type='dashed' danger></Button>}
            <Table scroll={{ y: '400px' }} pagination={false} style={{ marginTop: '10px' }} size="small" columns={columns} dataSource={historyFiles} />


        </div>
    )
}
export default connect((state: any, props: any) => ({
    ...props,
}), {
    loading_visible,
    loading_text,
    loading_status
})(View)